h1 {
	color: var(--pink);
}

.Read h1 {
	font-size: 2.5rem !important;
	margin-bottom: 0.5rem;
	color: #fff;
}

.Information {
	opacity: 0.75;
}

.Bar {
	margin: 2rem 0;
	padding: 1rem;
	border: 1px solid var(--pink);
	background: var(--darker);
	border-radius: 10px;
}

.Content {
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
		'Lucida Sans', Arial, sans-serif;
	font-size: 1.1rem;
	line-height: 1.5;
	opacity: 0.9;
}

.Content h1,
.Content h2,
.Content h3,
.Content h4,
.Content h5,
.Content h6 {
	color: #fff;
	margin: 2rem 0 1rem 0;
}

.Content li {
	margin: 0 0 2rem 2rem;
}

.Content p {
	margin-bottom: 1.5rem;
}

.Footer {
	padding: 3rem 0;
	font-weight: bold;
	font-size: 1.25rem;
	color: var(--pink);
}
